"use client";

import { Checkbox, Input, InputNumber, InputRef } from "antd";
import { useEffect, useRef, useState } from "react";
import TagInput from "../components/TagInput/TagInput";

export default function Form() {
    const inputRef = useRef<InputRef>(null);
    const [sliderValue, setSliderValue] = useState<{ FromSlider: number; ToSlider: number }>({
        FromSlider: 0,
        ToSlider: 0
    });
    const [autoResponses, setAutoResponses] = useState(false); // Для чекбокса

    // Обработчик для изменения значений слайдера
    const handleSliderChange = (value: number, type: 'FromSlider' | 'ToSlider') => {
        setSliderValue((prev) => ({
            ...prev,
            [type]: value
        }));
    };



    return (
        <form className="max-w-md mx-auto p-4 bg-white shadow rounded">
            <h2 className="text-lg text-cyan-700 font-semibold mb-4">Создать задачу</h2>
            <Input className="w-full mb-5 p-2 border rounded" placeholder="Название задачи" />
            <Input className="w-full p-2 border rounded" placeholder="Описание" />

            {/* Добавление тега */}
            <TagInput />
            {/* Слайдеры */}
            <div>
                <label className="text-cyan-700 text-sm">
                    Минимальный бюджет
                </label>
                <Input
                    type="number"
                    value={sliderValue.FromSlider}
                    onChange={(e) => handleSliderChange(Number(e.target.value), 'FromSlider')}
                />
                <label className="text-cyan-700 text-sm">
                    Максимальный бюджет
                </label>
                <Input
                    type="number"
                    value={sliderValue.ToSlider}
                    onChange={(e) => handleSliderChange(Number(e.target.value), 'ToSlider')}
                />
            </div>

            {/* Срок в днях */}
            <div>
                <label className="text-cyan-700 text-sm">Срок в днях</label>
                <Input
                    type="number"
                    defaultValue={1}
                />
            </div>

            {/* Количество напоминаний */}
            <div>
                <div>
                    <span className="text-cyan-700 text-sm">Количество напоминаний</span>
                </div>
                <InputNumber defaultValue={1} />
            </div>

            {/* Чекбокс для включения автоматических ответов */}
            <div className="flex items-center space-x-2 pt-2">
                <Checkbox
                    id="allAutoResponses"
                    checked={autoResponses}
                    onChange={(e) => setAutoResponses(e.target.checked)}
                    className="h-4 w-4 text-blue-600 border-gray-300 rounded"
                />
                <label className="text-sm text-gray-700">Включить автоматические ответы</label>
            </div>

            {/* Кнопка отправки */}
            <button className="mt-4 w-full bg-blue-500 text-white py-2 rounded">Отправить</button>
        </form>
    );
}
